.pointShopPage {
    width: 100%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    background-color: #fff;
    position: static;
    overflow: visible;
    min-height: 100%;
}

/* ÌÉÄÏù¥ÌãÄÍ≥º Ìè¨Ïù∏Ìä∏ Ïª®ÌÖåÏù¥ÎÑà */
.headerContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 2.5rem 2.5rem 1rem 2.5rem;
    border-bottom: 2px solid #00753C;
    gap: 1rem;
}

.pageTitle {
    font-size: 2.2rem;
    font-weight: 600;
    color: #004225;
    line-height: 1.2;
    margin: 0;
    white-space: nowrap;
}

.pointBalance {
    font-size: 1.3rem;
    color: #00753C;
    font-weight: 600;
    white-space: nowrap;
}

/* ÏÉÅÌíà Î¶¨Ïä§Ìä∏ Ïä§ÌÉÄÏùº */
.productList {
    padding: 2rem 3rem; /* Ï¢åÏö∞ Ìå®Îî© Í∑†Îì±ÌïòÍ≤å 3rem */
    width: 100%;
    max-width: 1200px; /* ÏµúÎåÄ ÎÑìÏù¥ ÏÑ§Ï†ï, ÌïÑÏöîÏãú Ï°∞Ï†à */
    margin: 0 auto; /* Ï§ëÏïô Ï†ïÎ†¨ */
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 2.5rem;
    justify-items: center;
}


/* Ïπ¥Îìú Ï†ïÏÇ¨Í∞ÅÌòï ÎßåÎì§Í∏∞ (widthÏóê ÎßûÏ∂∞ height Í≤∞Ï†ï) */
.productCard {
    background-color: #fff;
    border: 2px solid #00753C;
    border-radius: 12px;
    width: 100%;
    max-width: 220px;
    aspect-ratio: 1 / 1; /* Ï†ïÏÇ¨Í∞ÅÌòï ÎπÑÏú® Ïú†ÏßÄ */
    padding: 1rem;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: box-shadow 0.3s ease, transform 0.3s ease;
    cursor: pointer;
    user-select: none;
}

.productCard:hover {
    transform: translateY(-6px);
}

.productImage {
    width: 100%;
    height: 60%; /* Ïπ¥Îìú ÎÜíÏù¥ ÎåÄÎπÑ Ïù¥ÎØ∏ÏßÄ ÎπÑÏú® Ï°∞Ï†ï */
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 0.8rem;
    flex-shrink: 0;
}

.productName {
    font-size: 1.2rem;
    color: #004225;
    margin-bottom: 0.4rem;
    text-align: center;
    min-height: 2.4rem;
    flex-grow: 0;
    flex-shrink: 0;
}

.productPrice {
    font-size: 1rem;
    color: #00753C;
    font-weight: 600;
    margin-bottom: 0;
    flex-grow: 0;
}

/* ÏÉÅÌíà ÏóÜÏùÑ Îïå Î©îÏãúÏßÄ Ïä§ÌÉÄÏùº */
.emptyMessage {
    text-align: center;
    color: #666;
    font-size: 1.5rem;
    font-weight: bold;
}

.nothing {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #666;
    font-size: 1.5rem;
    font-weight: bold;
    width: 100%;
    max-width: 400px;
    padding: 0 1rem;
}

/* Î∞òÏùëÌòï */
@media (max-width: 768px) {
    .headerContainer {
        flex-direction: column;
        align-items: center;
        gap: 0.5rem;
        padding: 2rem 1rem 1rem 1rem;
    }

    .pageTitle {
        font-size: 1.8rem;
    }

    .pointBalance {
        font-size: 1.1rem;
    }

    .productList {
        padding-left: 1rem;
        padding-right: 1rem;
        justify-content: center;
        grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
        gap: 1.5rem 1.5rem;
    }

    .productCard {
        max-width: 150px;
        aspect-ratio: 1 / 1;
        padding: 0.8rem;
    }

    .productImage {
        height: 60%;
        margin-bottom: 0.6rem;
    }

    .productName {
        font-size: 1rem;
        min-height: 1.8rem;
        margin-bottom: 0.3rem;
    }

    .productPrice {
        font-size: 0.9rem;
    }
}
