.floatingBtn {
    position: fixed;         /* ÌôîÎ©¥Ïóê Í≥†Ï†ï */
    bottom: 20px;            /* ÌïòÎã®ÏóêÏÑú 20px Îñ®Ïñ¥Ïßê */
    right: 20px;             /* Ïö∞Ï∏°ÏóêÏÑú 20px Îñ®Ïñ¥Ïßê */
    padding: 15px 25px;
    background-color: #00753C;
    color: white;
    border: none;
    border-radius: 50px;
    font-size: 1.2rem;
    cursor: pointer;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s;
    z-index: 9999;           /* Ï†úÏùº ÏúÑÎ°ú Ïò¨ÎùºÏò§ÎèÑÎ°ù ÏÑ§Ï†ï */
}

.floatingBtn:hover {
    background-color: #005b2c;
}

@media (max-width: 768px) {
