/* ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ
   Forgot.module.css
   ‚ÄúÎπÑÎ∞ÄÎ≤àÌò∏ Ï∞æÍ∏∞‚Äù ÌéòÏù¥ÏßÄ Ï†ÑÏ≤¥ Ïä§ÌÉÄÏùº
‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ‚îÄ */

/* 1) ÌôîÎ©¥ Î∞∞Í≤Ω, Ï†ÑÏ≤¥ Ïª®ÌÖåÏù¥ÎÑà */
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    padding: 2rem;
    background-color: #FCFBF2;
}

/* 2) ÏÉÅÎã® Î°úÍ≥† Ïù¥ÎØ∏ÏßÄ */
.logoImg {
    width: 180px;      /* Í∏∞Ï°¥ 140px ‚Üí 180pxÎ°ú ÌôïÎåÄ */
    margin-bottom: 1.5rem;
    cursor: pointer;
}

/* 3) Ìù∞ÏÉâ Ïπ¥Îìú Î∞ïÏä§ (ÎπÑÎ∞ÄÎ≤àÌò∏ Ï∞æÍ∏∞ Ìèº Ï†ÑÏ≤¥Î•º Í∞êÏã∏Îäî Î∞ïÏä§) */
.box {
    display: flex;
    flex-direction: column;
    gap: 2rem;           /* ‚ë† Ìèº Ï†ÑÏ≤¥ ‚Üî Ìë∏ÌÑ∞ ÏÇ¨Ïù¥ Í∞ÑÍ≤©ÏùÑ 2rem Ï§å */
    width: 400px;
    background-color: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
    padding: 2rem;  /* ÏúÑ/ÏïÑÎûò 3rem, Ï¢å/Ïö∞ 2rem */
    border: 1px solid #e0e5e2;
}

/* 4) Ï†úÎ™© Ïä§ÌÉÄÏùº */
.title {
    font-size: 1.5rem;    /* Ï†úÎ™© Í∏ÄÏûê ÌÅ¨Í∏∞ */
    font-weight: 600;     /* ÏßÑÌïú Í∏ÄÏî® */
    color: #333333;       /* ÏßôÏùÄ ÌöåÏÉâ ÌÖçÏä§Ìä∏ */
    text-align: center;   /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
    margin: 0;            /* Í∏∞Î≥∏ ÎßàÏßÑ Ï†úÍ±∞ */
}

/* 5) Ìèº ÎÇ¥Î∂Ä ÏöîÏÜå Î∞∞Ïπò: Ïù∏Ìíã 3Í∞ú + Î≤ÑÌäº ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
.form {
    display: flex;
    flex-direction: column;
    gap: 1.8rem;
}

/* 6) ÏûÖÎ†•(input) Í≥µÌÜµ Ïä§ÌÉÄÏùº */
.input {
    width: 100%;
    padding: 1rem;
    border: 1px solid #ccd8ce;     /* Ïó∞Ìïú ÌöåÎÖπÏÉâ ÌÖåÎëêÎ¶¨ */
    border-radius: 6px;            /* Î™®ÏÑúÎ¶¨ Îë•Í∏ÄÍ≤å */
    font-size: 1rem;               /* Í∏ÄÏûê ÌÅ¨Í∏∞ */
    transition: border-color 0.2s ease, box-shadow 0.2s ease;
    background-color: #ffffff;     /* ÏïÑÏ£º Ïó∞Ìïú ÌöåÏÉâ Î∞∞Í≤Ω */
}

/* Ïù∏Ìíã Ìè¨Ïª§Ïä§ ÏÉÅÌÉú(ÌÅ¥Î¶≠ Ïãú) Ïä§ÌÉÄÏùº */
.input:focus {
    outline: none;
    border-color: #3b6d41;         /* ÏßÑÌïú Ï¥àÎ°ùÏÉâ ÌÖåÎëêÎ¶¨ */
    box-shadow: 0 0 0 3px rgba(59, 109, 65, 0.15);
}

/* ÌîåÎ†àÏù¥Ïä§ÌôÄÎçî ÏÉâÏÉÅ (Ïó∞Ìïú ÌöåÎÖπÏÉâ) */
.input::placeholder {
    color: #8e9a94;
    font-weight: 500;
}
.input::-webkit-input-placeholder {
    color: #8e9a94;
    font-weight: 500;
}
.input::-moz-placeholder {
    color: #8e9a94;
    opacity: 1;
    font-weight: 500;
}
.input:-ms-input-placeholder {
    color: #8e9a94;
    font-weight: 500;
}
.input::-ms-input-placeholder {
    color: #8e9a94;
    font-weight: 500;
}

/* 7) ÎπÑÎ∞ÄÎ≤àÌò∏ Ï∞æÍ∏∞ Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.button {
    width: 100%;
    padding: 0.85rem 0;
    background-color: #3b6d41;    /* ÏßôÏùÄ Ï¥àÎ°ùÏÉâ Î∞∞Í≤Ω */
    color: #ffffff;               /* Î≤ÑÌäº Í∏ÄÏûêÎäî Ìù∞ÏÉâ */
    font-size: 1rem;
    font-weight: 600;
    border: none;
    border-radius: 6px;           /* Î™®ÏÑúÎ¶¨ Îë•Í∏ÄÍ≤å */
    cursor: pointer;
    transition: background-color 0.2s ease, transform 0.1s ease;
}

/* Î≤ÑÌäº Ìò∏Î≤Ñ(hover) Ïãú */
.button:hover {
    background-color: #5a8d6b;    /* ÏÇ¥Ïßù Î∞ùÏùÄ Ï¥àÎ°ùÏúºÎ°ú Î≥ÄÍ≤Ω */
    transform: translateY(-1px);  /* ÏÇ¥Ïßù Îñ†Ïò§Î•¥Îäî Ìö®Í≥º */
}

/* 8) ÌïòÎã® Ìë∏ÌÑ∞: ‚ÄúÎ°úÍ∑∏Ïù∏ÏúºÎ°ú ÎèåÏïÑÍ∞ÄÍ∏∞‚Äù ÎßÅÌÅ¨ */
.footer {
    text-align: center;
    font-size: 0.9rem;
