}

/* Ìë∏ÌÑ∞ ÎßÅÌÅ¨(.link) Ïä§ÌÉÄÏùº */
.link {
    text-decoration: none;
    color: #3b6d41;               /* ÏßôÏùÄ Ï¥àÎ°ùÏÉâ */
    font-weight: 500;
    transition: color 0.2s ease;
}

/* ÎßÅÌÅ¨ Ìò∏Î≤Ñ Ïãú ÏÉâÏÉÅ Î≥ÄÍ≤Ω */
.link:hover {
    color: #5a8d6b;               /* Î∞ùÏùÄ Ï¥àÎ°ùÏúºÎ°ú Î≥ÄÍ≤Ω */
}
